@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap");
:root {
  --vd-Blue: hsl(243, 87%, 12%);
  --d-Blue: hsl(238, 22%, 44%);
  --b-Blue: hsl(224, 93%, 58%);
  --m-Cyan: hsl(170, 45%, 43%);
  --lg-Blue: hsl(240, 75%, 98%);
  --l-Gray: hsl(0, 0%, 75%);
}

:root {
  --font-OpenSans: "Open Sans", sans-serif;
  --fontRaleway: "Raleway", sans-serif;
  --fontBold: 700;
  --fontNormal: 400;
}

@keyframes errorPop {
  0% {
    transform: translateX(-100px);
  }
  100% {
    transform: translateX(0);
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  font-family: var(--font-OpenSans);
}

body {
  height: 100%;
  width: 100%;
}

img {
  height: auto;
  max-width: 100%;
}

ul,
ol {
  list-style: none;
}

button,
input {
  font-family: var(--fontNormal);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 68.75rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: var(--fontRaleway);
}

p {
  margin: 0;
}

a:focus {
  outline: none;
}
a:focus-visible {
  outline: 0.1875rem dashed var(--d-Blue);
  outline-offset: 0.1875rem;
}

.heading, .heading--l, .heading--xl, .heading--xxl {
  color: var(--vd-Blue);
}
.heading--xxl {
  font-size: clamp(1.625rem, 4vw, 2.6875rem);
  line-height: 3.375rem;
  margin-bottom: 1.25rem;
}
.heading--xl {
  font-size: clamp(1.125rem, 4vw, 2.3125rem);
  text-align: center;
  margin-bottom: 1.625rem;
}
@media (min-width: 50em) {
  .heading--xl {
    text-align: left;
  }
}
.heading--l {
  font-size: clamp(1.3125rem, 5vw, 1.875rem);
  color: var(--lg-Blue);
  margin-bottom: 0.875rem;
}

.paragraph {
  color: var(--d-Blue);
}
.paragraph--hero {
  font-size: clamp(0.9rem, 3vw, 1.2625rem);
  line-height: 1.25rem;
  max-width: 650px;
}
.paragraph--about {
  font-size: clamp(0.9375rem, 3vw, 1rem);
  margin-bottom: 1.625rem;
}
.paragraph--cta {
  font-size: 0.9375rem;
  color: var(--lg-Blue);
  max-width: 28.75rem;
  line-height: 1.4375rem;
}

.btn {
  position: relative;
  cursor: pointer;
  border: 0;
  outline: none;
}
.btn:hover {
  opacity: 0.7;
  transition: 0.3s ease;
}
.btn:focus {
  outline: none;
}
.btn:focus-visible {
  outline: 0.1875rem dashed var(--d-Blue);
  outline-offset: 0.1875rem;
}
.btn--blue {
  background-color: var(--b-Blue);
  color: var(--lg-Blue);
  font-size: 1rem;
  border-radius: 0.25rem;
  height: 2.5625rem;
  margin-top: 0.625rem;
}
.btn--blue:focus {
  outline: none;
}
.btn--blue:focus-visible {
  outline: 0.1875rem dashed var(--lg-Blue);
  outline-offset: 0.1875rem;
}
@media (min-width: 50em) {
  .btn--blue {
    width: 13.125rem;
    height: 2.875rem;
    margin-top: 1.25rem;
  }
}
.btn--cta {
  background-color: var(--b-Blue);
  color: var(--lg-Blue);
  height: 2.5625rem;
  border-radius: 0.25rem;
  margin-top: 0.625rem;
  overflow: hidden;
  font-size: 1rem;
  position: relative;
}
@media (min-width: 50em) {
  .btn--cta {
    margin: 0 1.25rem;
    width: 12.5rem;
    height: 2.875rem;
  }
}

.header {
  position: relative;
  padding: 1.5625rem 0;
  margin-bottom: 2.8125rem;
}
@media (min-width: 64em) {
  .header {
    padding: 3.625rem 2.5625rem 2rem;
  }
}
.header__container {
  max-width: 81.375rem;
}
.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .nav__logo {
  width: 6.25rem;
}
@media (min-width: 50em) {
  .header .nav__logo {
    width: 100%;
  }
}
.header .nav__list {
  display: flex;
  align-items: center;
}
.header .nav__list--item {
  font-size: clamp(0.9rem, 1.5vw, 1rem);
  color: var(--vd-Blue);
  position: relative;
}
.header .nav__list--item:not(:last-child) {
  margin-right: 1.3125rem;
}
@media (min-width: 64em) {
  .header .nav__list--item:not(:last-child) {
    margin-right: 3.5rem;
  }
}
.header .nav__list--item::before {
  content: "";
  position: absolute;
  bottom: -0.3125rem;
  right: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--vd-Blue);
  transform: scale(0) translateY(-0.5625rem);
  opacity: 0;
  transition: all 350ms ease;
}
@media (min-width: 50em) {
  .header .nav__list--item:hover::before {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.hero {
  position: relative;
  padding: 0 1rem 9.625rem;
}
.hero__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 82.4375rem;
}
@media (min-width: 50em) {
  .hero__container {
    flex-direction: row-reverse;
  }
}
.hero__image {
  margin-bottom: 1.25rem;
}
@media (min-width: 50em) {
  .hero__image {
    flex: 1;
  }
}
.hero__content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media (min-width: 50em) {
  .hero__content {
    text-align: left;
    flex: 1;
  }
}
@media (min-width: 64em) {
  .hero__content {
    margin-top: -3.8125rem;
  }
}
.hero__form {
  margin-top: 1.6875rem;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}
@media (min-width: 50em) {
  .hero__form {
    flex-direction: row;
  }
}
.hero__form--error {
  visibility: hidden;
  font-size: 0.75rem;
  text-align: left;
  color: red;
  margin-top: -1.125rem;
  height: 0;
  opacity: 0;
}
@media (min-width: 50em) {
  .hero__form--error {
    position: absolute;
    top: 4.125rem;
    left: 0;
  }
}
.hero__form--error.error {
  visibility: visible;
  opacity: 1;
  height: 1.25rem;
}
.hero__form--error.success {
  visibility: visible;
  opacity: 1;
  height: 1.25rem;
  color: green;
}
.hero__form--input {
  height: 2.5625rem;
  border: 1px solid var(--vd-Blue);
  border-radius: 0.25rem;
  margin-bottom: 1.25rem;
  padding-left: 1rem;
  outline: none;
  background-color: var(--lg-Gray);
  position: relative;
}
@media (min-width: 50em) {
  .hero__form--input {
    width: 27vw;
    height: 2.875rem;
    margin: 0;
  }
}
.hero__form--input.error {
  border: 0.125rem solid red;
}
.hero__form--input.success {
  border: 0.125rem solid green;
}
.hero__form--input:focus {
  outline: 0.1875rem dashed var(--d-Blue);
  outline-offset: 0.1875rem;
}

.about {
  position: relative;
  background-image: url("/images/bg-curve-mobile.svg");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10.875rem 0 2.8125rem;
}
@media (min-width: 50em) {
  .about {
    background-image: url("/images/bg-curve-desktop.svg");
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.about__container {
  display: flex;
  flex-direction: column;
  max-width: 81.6875rem;
}
@media (min-width: 50em) {
  .about__container {
    flex-direction: row;
  }
}
.about__image {
  margin-bottom: 4.375rem;
}
@media (min-width: 50em) {
  .about__image {
    flex: 1;
    order: 2;
  }
}
.about__content {
  display: flex;
  flex-direction: column;
}
@media (min-width: 50em) {
  .about__content {
    flex: 1;
  }
}
.about__link {
  text-align: center;
}
@media (min-width: 50em) {
  .about__link {
    text-align: left;
  }
}
.about__link--see {
  color: var(--m-Cyan);
  text-align: center;
  position: relative;
}
.about__link--see:hover {
  color: #39d5bb;
}
.about__link--see:hover::after {
  background-color: #39d5bb;
}
.about__link--see::after {
  content: "";
  position: absolute;
  bottom: -0.3125rem;
  right: 0;
  height: 0.125rem;
  width: 100%;
  background-color: var(--m-Cyan);
}
.about__testimonial {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2.75rem auto 0;
  max-width: 20rem;
  box-shadow: 1px 1px 0.625rem rgba(0, 0, 0, 0.1);
  padding: 2.5rem 0.9375rem 1.375rem 1.5625rem;
}
@media (min-width: 50em) {
  .about__testimonial {
    margin: 2.75rem 0;
  }
}
.about__testimonial-text {
  font-size: 0.9375rem;
  color: var(--vd-Blue);
  position: relative;
}
.about__testimonial-text::before {
  content: url("/images/icon-quotes.svg");
  position: absolute;
  top: -1.4375rem;
}
.about__testimonial-author {
  display: flex;
  flex-direction: column;
  margin-left: 0.625rem;
}
.about__testimonial-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  padding-top: 1.25rem;
}
.about__testimonial-wrapper img {
  border-radius: 50%;
  width: 2.1875rem;
}
.about__testimonial-name, .about__testimonial-position {
  font-size: 0.6875rem;
}

.cta {
  position: relative;
  background-color: var(--d-Blue);
}
.cta__container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding: 3.125rem 2.5rem;
  text-align: center;
  max-width: 81.6875rem;
}
@media (min-width: 50em) {
  .cta__container {
    flex-direction: row;
    text-align: left;
    padding: 6.25rem 2.5rem;
  }
}
@media (max-width: 49.9375em) {
  .cta__content {
    margin-bottom: 1.25rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.cta__forms {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}
.cta__form--input {
  height: 2.5625rem;
  border: 1px solid var(--vd-Blue);
  border-radius: 0.25rem;
  margin-bottom: 1.25rem;
  padding-left: 1rem;
  outline: none;
  background-color: var(--lg-Blue);
  position: relative;
}
.cta__form--input.error {
  border: 0.125rem solid red;
}
.cta__form--input.success {
  border: 0.125rem solid green;
}
@media (min-width: 50em) {
  .cta__form--input {
    width: 34vw;
    height: 2.875rem;
    margin: 0;
  }
}
.cta__form--input:focus {
  outline: 0.1875rem dashed var(--lg-Blue);
  outline-offset: 0.1875rem;
}

.footer {
  background-color: var(--vd-Blue);
  padding: 6.75rem 1rem 0;
}
.footer__container {
  max-width: 81.6875rem;
  display: grid;
  grid-gap: 0.625rem;
  grid-template-columns: 3fr 2fr 3fr;
  grid-template-rows: 1fr 14.5rem;
  grid-template-areas: "logo logo logo" "info nav icon";
}
@media (max-width: 49.9375em) {
  .footer__container {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas: "logo" "info" "nav" "icon";
  }
}
.footer__logo {
  grid-area: logo;
}
.footer__info {
  grid-area: info;
  color: var(--lg-Blue);
}
.footer__info-location {
  margin: 1.25rem 0;
}
.footer__info span {
  margin-left: 1.25rem;
}
.footer__nav {
  grid-area: nav;
  display: flex;
  justify-content: space-between;
  margin-top: 1.25rem;
}
@media (max-width: 49.9375em) {
  .footer__nav {
    flex-direction: column;
  }
}
.footer__nav-list {
  margin: 0;
  padding: 0;
}
.footer__nav-item {
  margin-bottom: 0.875rem;
}
.footer__nav-link {
  color: var(--lg-Blue);
  position: relative;
}
.footer__nav-link:focus-visible {
  outline: 0.125rem dashed var(--lg-Blue);
  outline-offset: 0.125rem;
}
.footer__nav-link::before {
  content: "";
  position: absolute;
  bottom: -0.3125rem;
  right: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--lg-Blue);
  transform: scale(0) translateY(-0.5625rem);
  opacity: 0;
  transition: all 350ms ease;
}
@media (min-width: 50em) {
  .footer__nav-link:hover::before {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}
.footer__icon {
  grid-area: icon;
  text-align: center;
  margin-top: 1.25rem;
}
.footer__icon .social-icon {
  margin-right: 0.625rem;
}
.footer__icon .social-icon:focus {
  outline: none;
}
.footer__icon .social-icon:focus-visible {
  outline: 2px dashed var(--lg-Blue);
  outline-offset: 2px;
}
.footer__icon .social-icon path {
  transition: fill 0.3s ease;
}
.footer__icon .social-icon:hover path {
  fill: var(--b-Blue);
}